import React from "react";
import { motion } from "framer-motion";

const Header = () => {
  return (
    <motion.div
      initial={{ opacity: 0, y: -180 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{
        ease: "easeInOut",
        duration: 1,
        delay: 0.6,
      }}
      className="header"
    >
      <div className="header-inner">
        <div className="logo">zilasaule</div>
        <nav className="nav">
          <li>
            <a href="/design">crew</a>
          </li>
          <li>
            <a href="/strategy">ships</a>
          </li>
          <li>
            <a href="/cases">islands</a>
          </li>
          <li>
            <a href="/about">about</a>
          </li>
          <li>
            <a href="/why">resources</a>
          </li>
        </nav>
        <div className="contact">
          <a href="/contact">mint your crew in locotron</a>
        </div>
        <div className="hamburger-menu">
          <span></span>
          <span></span>
        </div>
      </div>
    </motion.div>
  );
};

export default Header;
