//--------------------three---------------
const WaveShaderMaterial = shaderMaterial(
  //uniforms
  {
    uTime: 0,
    uColor: new THREE.Color(0.0, 0.0, 0.0),
    uTexture: new THREE.Texture(),
    transparent: true, // TURNS ALPHA ON!!!!
  },

  // { blending: THREE.AdditiveBlending, depthTest: false, transparent: true },

  //vertex shader
  glsl`
 precision mediump float;

 varying vec2 vUv;
 varying float vWave;

 uniform float uTime;

 #pragma glslify: snoise3 = require(glsl-noise/simplex/3d.glsl);

 void main() {
vUv = uv;

vec3 pos = position;
float noiseFreq = 2.0;
float noiseAmp = 0.4;
vec3 noisePos = vec3(pos.x * noiseFreq + uTime, pos.y, pos.z);
pos.z += snoise3(noisePos) * noiseAmp;
vWave = pos.z;

gl_Position = projectionMatrix * modelViewMatrix * vec4(pos, 1.0);
 }
`,
  //fragment shader
  glsl`
precision mediump float;

uniform vec3 uColor;
uniform float uTime;
uniform sampler2D uTexture;

varying vec2 vUv;
varying float vWave;

void main() {
float wave = vWave * 0.2;

// if(texture2D(uTexture, vUv + wave).a != 1.0f)
//     {
//         discard;
//     }

// vec4 texture = texture2D(uTexture, vUv + wave).rgba;

//vec4 outputColor = vec4(mix(grundColor.rgb, cloudColor.rgb, cloudColor.a), 1.0);

gl_FragColor = texture2D(uTexture, vUv + wave);
// gl_FragColor = texture2D(uTexture, vUv + wave);

// gl_FragColor = vec4(tecture.rgb, texture.a);

 //gl_FragColor = vec4(cos(vUv.y * uTime) * uColor, 1.0);
//gl_FragColor = vec4(vUv.x * uColor, 1.0);
}`
);

extend({ WaveShaderMaterial });

const Wave = () => {
  const ref = useRef();
  useFrame(({ clock }) => (ref.current.uTime = clock.getElapsedTime()));

  const [image] = useLoader(THREE.TextureLoader, ["/images/tisland001.png"]);
  return (
    <mesh>
      <planeBufferGeometry args={[0.4, 0.4, 16, 16]} />
      <waveShaderMaterial uColor={"yellow"} ref={ref} uTexture={image} />
    </mesh>
  );
};

const Scene = () => {
  return (
    <Canvas camera={{ fov: 5 }}>
      <Suspense fallback={null}>
        <Wave />
      </Suspense>
    </Canvas>
  );
};

const App3 = () => {
  return <Scene />;
};

