import React, { useEffect } from "react";
import { motion } from "framer-motion";
import Image from "./Image";

const container = {
  show: {
    transition: {
      staggerChildren: 0.35,
    },
  },
};
const item = {
  hidden: { opacity: 0, y: 200 },
  show: {
    opacity: 1,
    y: 0,
    transition: { ease: [0.6, 0.01, -0.05, 0.95], duration: 1.6 },
  },
  exit: {
    opacity: 0,
    y: -200,
    transition: { ease: "easeInOut", duration: 0.8 },
  },
};

const itemMain = {
  hidden: { opacity: 0, y: 200 },
  show: {
    opacity: 1,
    y: 0,
    transition: { ease: [0.6, 0.01, -0.05, 0.95], duration: 1.6 },
  },
};

const Loader = ({ setLoading }) => {
  return (
    <div className="loader">
      <motion.div
        className="loader-inner"
        variants={container}
        initial="hidden"
        animate="show"
        exit="exit"
        onAnimationComplete={() => setLoading(false)}
      >
        <ImageBlock variants={item} id="bship001" />
        <motion.div variants={itemMain} className="transition-image">
          <motion.img
            layoutId="main-bg"
            src={process.env.PUBLIC_URL + `/images/tisland001.png`}
            alt="random alt"
          />
        </motion.div>
        <ImageBlock variants={item} id="fship002" />
        <ImageBlock variants={item} id="fship001" />
        <ImageBlock variants={item} id="fisland002" />
      </motion.div>
    </div>
  );
};

export const ImageBlock = ({ id, variants }) => {
  return (
    <motion.div className={`image-block ${id}`} variants={variants}>
      <Image
        src={process.env.PUBLIC_URL + `/images/${id}.png`}
        //fallback={process.env.PUBLIC_URL + `/images/${id}.jpg`}
        alt={id}
      />
    </motion.div>
  );
};
export default Loader;
